import React, { useEffect, useState } from 'react';
import './RoomMatrix.css';
import roomBg from './assets/room_matrix.png'; //  Correct image import

function RoomMatrix() {
  const [roomData, setRoomData] = useState({
    temperature: 0,
    pressure: 0,
    motion: false,
    airQuality: 0,  // Set air quality to 0
    lighting: 0,
  });

  // Simulating data fetch every 30 seconds
  useEffect(() => {
    const fetchRoomData = async () => {
      // Simulating room data with moderate values
      setRoomData({
        temperature: (Math.random() * 5 + 20).toFixed(1), // 20C to 25C
        pressure: (Math.random() * 20 + 1000).toFixed(1), // 1000 hPa to 1020 hPa
        motion: Math.random() > 0.5, // Alternate true/false for motion detection (50% chance)
        airQuality: 0, // Air quality fixed at 0
        lighting: Math.floor(Math.random() * 31 + 50), // 50% to 80% lighting
      });
    };

    fetchRoomData();
    const interval = setInterval(fetchRoomData, 30000); // 30 seconds = 30000 ms

    return () => clearInterval(interval);
  }, []);

  return (
    <div
      className="room-matrix-container"
      style={{
        backgroundImage: `url(${roomBg})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        minHeight: '100vh',
        padding: '2rem',
        color: '#fff',
      }}
    >
      <h2>Real-Time Room Monitoring</h2>
      <div className="room-matrix">
        <div className="room-matrix-item">
          <h3>Temperature</h3>
          <p>{roomData.temperature} C</p>
        </div>
        <div className="room-matrix-item">
          <h3>Pressure</h3>
          <p>{roomData.pressure} hPa</p>
        </div>
        <div className="room-matrix-item">
          <h3>Motion</h3>
          <p>{roomData.motion ? 'Detected' : 'Not Detected'}</p>
        </div>
        <div className="room-matrix-item">
          <h3>Air Quality</h3>
          <p>{roomData.airQuality} AQI</p>
        </div>
        <div className="room-matrix-item">
          <h3>Lighting</h3>
          <p>{roomData.lighting} %</p>
        </div>
      </div>
    </div>
  );
}

export default RoomMatrix;
