import React, { useState, useEffect } from 'react';
import {
  LineChart, Line, XAxis, YAxis, Tooltip, Legend, CartesianGrid,
  PieChart, Pie, RadarChart, PolarGrid, PolarAngleAxis, PolarRadiusAxis, Radar,
  BarChart, Bar, Cell
} from 'recharts';
import './BInsight.css';
import binsightImage from './assets/binsight.png'; // Import the image

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042']; // Color palette for pie chart

function BInsight() {
  const [timeframe, setTimeframe] = useState('daily');
  const [chartType, setChartType] = useState('pie'); // Pie chart for emotion
  
  // Static data for activity, sleep, and emotion (updated to match requirements)
  const initialActivityData = [
    { time: '08:00', walking: 30, resting: 80, falling: 10, exercising: 10 },
    { time: '08:15', walking: 40, resting: 75, falling: 5, exercising: 15 },
    { time: '08:30', walking: 45, resting: 70, falling: 5, exercising: 15 },
    { time: '08:45', walking: 50, resting: 65, falling: 5, exercising: 15 },
    { time: '09:00', walking: 60, resting: 60, falling: 5, exercising: 10 },
    { time: '09:15', walking: 70, resting: 55, falling: 5, exercising: 10 }
  ];

  const initialSleepData = [
    { type: 'Deep Sleep', value: 5 },
    { type: 'Light Sleep', value: 3 },  // Set a smaller value for Light Sleep
    { type: 'Awake', value: 6 }         // Set a larger value for Awake
  ];

  const initialEmotionData = [
    { type: 'Relaxed', value: 30 },
    { type: 'Stressed', value: 40 },
    { type: 'Motivated', value: 40 },
    { type: 'Anxious', value: 10 }
  ];

  const [activityData, setActivityData] = useState(initialActivityData);
  const [sleepData, setSleepData] = useState(initialSleepData);
  const [emotionData, setEmotionData] = useState(initialEmotionData);

  // Simulate data updates every 2 seconds
  useEffect(() => {
    const updateData = () => {
      setActivityData((prevData) => {
        return prevData.map((entry) => ({
          ...entry,
          resting: entry.resting + Math.floor(Math.random() * 5) // Increase resting over time
        }));
      });

      setSleepData((prevData) => {
        return prevData.map((entry) => {
          if (entry.type === 'Light Sleep') {
            return { ...entry, value: Math.max(2, entry.value + Math.floor(Math.random() * 1)) }; // Ensure Light Sleep is less than Awake
          } else if (entry.type === 'Awake') {
            return { ...entry, value: entry.value + Math.floor(Math.random() * 2) }; // Slightly increase Awake value
          }
          return entry;
        });
      });

      setEmotionData((prevData) => {
        return prevData.map((entry) => {
          if (entry.type === 'Stressed') {
            return { ...entry, value: entry.value + Math.floor(Math.random() * 3) }; // Increase stress
          } else if (entry.type === 'Motivated') {
            return { ...entry, value: entry.value + Math.floor(Math.random() * 2) }; // Increase motivation
          }
          return entry;
        });
      });
    };

    const interval = setInterval(updateData, 2000); // Update data every 2 seconds
    return () => clearInterval(interval); // Cleanup on component unmount
  }, []);

  return (
    <div className="binsight-container">
      <h2>Behavioral Insight ({timeframe.toUpperCase()})</h2>

      {/* Adding Image to BInsight */}
      <div className="binsight-image">
        <img src={binsightImage} alt="Behavioral Insights" />
      </div>

      <div className="toggle-buttons">
        <button onClick={() => setTimeframe('daily')} className={timeframe === 'daily' ? 'active' : ''}>Daily</button>
        <button onClick={() => setTimeframe('weekly')} className={timeframe === 'weekly' ? 'active' : ''}>Weekly</button>
      </div>

      {/*  Daily Activity Timeline */}
      <div className="chart-card">
        <h3> Daily Activity Timeline</h3>
        <LineChart width={600} height={300} data={activityData}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="time" />
          <YAxis />
          <Tooltip />
          <Legend />
          <Line type="monotone" dataKey="walking" stroke="#0088FE" />
          <Line type="monotone" dataKey="resting" stroke="#00C49F" />
          <Line type="monotone" dataKey="falling" stroke="#FFBB28" />
          <Line type="monotone" dataKey="exercising" stroke="#FF8042" />
        </LineChart>
      </div>

      {/*  Emotion Distribution (Pie chart) */}
      <div className="chart-card">
        <h3> Emotion Distribution</h3>
        <button onClick={() => setChartType(chartType === 'pie' ? 'radar' : 'pie')}>
          Switch to {chartType === 'pie' ? 'Radar' : 'Pie'}
        </button>
        {chartType === 'pie' ? (
          <PieChart width={400} height={300}>
            <Pie
              data={emotionData}
              dataKey="value"
              nameKey="type"
              cx="50%"
              cy="50%"
              outerRadius={100}
              label
            >
              {emotionData.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
              ))}
            </Pie>
            <Tooltip />
            <Legend />
          </PieChart>
        ) : (
          <RadarChart outerRadius={100} width={500} height={300} data={emotionData}>
            <PolarGrid />
            <PolarAngleAxis dataKey="type" />
            <PolarRadiusAxis />
            <Radar name="Emotion" dataKey="value" stroke="#8884d8" fill="#8884d8" fillOpacity={0.6} />
            <Tooltip />
            <Legend />
          </RadarChart>
        )}
      </div>

      {/*  Sleep Summary (Bar chart) */}
      <div className="chart-card">
        <h3> Sleep Summary</h3>
        <BarChart width={600} height={300} data={sleepData}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="type" />
          <YAxis />
          <Tooltip />
          <Legend />
          <Bar dataKey="value">
            {sleepData.map((entry, index) => (
              <Cell key={`bar-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Bar>
        </BarChart>
      </div>
    </div>
  );
}

export default BInsight;
